body {
  width: 100%;
  overflow-x: hidden;
  color: lightGrey;
}

@-webkit-keyframes animateAround {
  0%,
  24% {
    transorm: translateY(5px);
  }
  25%,
  49% {
    transfrom: translateX(5px);
  }
  50%,
  100% {
    -webkit-transform-origin: 0%;
            transform-origin: 0%;
  }
}

@keyframes animateAround {
  0%,
  24% {
    transorm: translateY(5px);
  }
  25%,
  49% {
    transfrom: translateX(5px);
  }
  50%,
  100% {
    -webkit-transform-origin: 0%;
            transform-origin: 0%;
  }
}

a {
  text-decoration: none;
}

